  var LITERAL = 'null Ð¸ÑÑ‚Ð¸Ð½Ð° Ð»Ð¾Ð¶ÑŒ Ð½ÐµÐ¾Ð¿Ñ€ÐµÐ´ÐµÐ»ÐµÐ½Ð¾';
  
  // number : Ñ‡Ð¸ÑÐ»Ð°
